"use client"

import React, { useState, useEffect } from "react"
import {
  Activity, Waves, Mic, Anchor, Aperture, Infinity, TrendingDown, Zap,
  Database, Cpu, ArrowUpRight, Box, Trash2, Atom, Terminal, FileCode
} from "lucide-react"
// Sửa đường dẫn import cho đúng cấu trúc thư mục
import { HoloCard, NeonButton } from "../ui/sci-fi-lab"

const EXPERIMENTS = [
  {
    id: "spring-oscillator",
    title: "Con lắc lò xo",
    icon: Activity,
    code: "EXP-01",
    desc: "Mô phỏng dao động điều hòa lò xo ngang/dọc."
  },
  {
    id: "simple-pendulum",
    title: "Con lắc đơn",
    icon: Anchor,
    code: "EXP-02",
    desc: "Khảo sát chu kỳ và quỹ đạo con lắc đơn."
  },
  {
    id: "damped-oscillation",
    title: "Dao động tắt dần",
    icon: TrendingDown,
    code: "EXP-03",
    desc: "Sự suy giảm năng lượng trong môi trường cản."
  },
  {
    id: "harmonic-oscillation",
    title: "Dao động điều hòa",
    icon: Zap,
    code: "EXP-04",
    desc: "Mối liên hệ chuyển động tròn đều."
  },
  {
    id: "mechanical-waves",
    title: "Sóng cơ",
    icon: Waves,
    code: "EXP-05",
    desc: "Truyền sóng ngang và dọc trong môi trường."
  },
  {
    id: "sound-waves",
    title: "Sóng âm",
    icon: Mic,
    code: "EXP-06",
    desc: "Phân tích tần số và biên độ âm thanh."
  },
  {
    id: "wave-interference",
    title: "Giao thoa sóng",
    icon: Aperture,
    code: "EXP-07",
    desc: "Hiện tượng giao thoa hai nguồn kết hợp."
  },
  {
    id: "standing-waves",
    title: "Sóng dừng",
    icon: Infinity,
    code: "EXP-08",
    desc: "Bụng sóng và nút sóng trên dây."
  },
  {
    id: "sound-speed",
    title: "Đo tốc độ truyền âm",
    icon: Mic,
    code: "EXP-09",
    desc: "Đo tốc độ âm bằng cột khí cộng hưởng."
  }
]

export default function PhysicsIntro({ onSelectExperiment }: { onSelectExperiment?: (id: string) => void }) {
  const [customModules, setCustomModules] = useState<any[]>([])

  useEffect(() => {
    fetch('/api/modules')
      .then(res => res.json())
      .then(data => setCustomModules(data))
      .catch(err => console.error(err))
  }, [])

  const handleDelete = async (e: React.MouseEvent, id: string) => {
    e.stopPropagation()
    if (!confirm("Bạn có chắc muốn xóa module này không?")) return

    try {
      const res = await fetch('/api/modules', {
        method: 'DELETE',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ id })
      })
      if (res.ok) {
        setCustomModules(prev => prev.filter(m => m.id !== id))
      } else {
        alert("Xóa thất bại")
      }
    } catch (err) {
      console.error(err)
      alert("Có lỗi xảy ra")
    }
  }

  return (
    <div className="space-y-8">
      {/* Hero Section */}
      <div className="relative rounded-2xl overflow-hidden border border-slate-800 bg-slate-900/50 p-8 md:p-12">
        <div className="absolute top-0 right-0 p-4 opacity-20">
          <Cpu size={200} className="text-cyan-500" />
        </div>

        <div className="relative z-10 max-w-2xl">
          <div className="inline-flex items-center gap-2 px-3 py-1 rounded-full bg-cyan-950/50 border border-cyan-800 text-cyan-400 text-xs font-bold uppercase tracking-wider mb-4">
            <span className="w-2 h-2 bg-cyan-400 rounded-full animate-pulse"></span>
            Phòng thí nghiệm ảo v2.0
          </div>
          <h1 className="text-4xl md:text-5xl font-bold text-white mb-4 tracking-tight">
            Chào mừng đến với <br />
            <span className="text-transparent bg-clip-text bg-gradient-to-r from-cyan-400 to-blue-500">PHÒNG THÍ NGHIỆM VẬT LÝ</span>
          </h1>
          <p className="text-slate-400 text-lg mb-8 leading-relaxed">
            Hệ thống mô phỏng tương tác cao cấp dành cho Vật lý lớp 11. <br />
            Truy cập dữ liệu thời gian thực, đồ thị năng lượng và các tham số vật lý chính xác.
          </p>

          <div className="flex gap-4">
            <NeonButton onClick={() => onSelectExperiment && onSelectExperiment('harmonic-oscillation')} active={true}>
              Bắt đầu ngay
            </NeonButton>
            <NeonButton variant="amber" onClick={() => window.open('https://github.com', '_blank')}>
              Tài liệu hướng dẫn
            </NeonButton>
          </div>
        </div>
      </div>

      {/* Grid Dashboard */}
      <div>
        <div className="flex items-center gap-2 mb-6 border-b border-slate-800 pb-2">
          <Database size={20} className="text-cyan-500" />
          <h2 className="text-lg font-bold text-slate-200 uppercase tracking-widest">Danh mục thí nghiệm</h2>
        </div>

        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4">
          {EXPERIMENTS.map((exp) => (
            <HoloCard key={exp.id} className="h-full flex flex-col hover:-translate-y-1 hover:shadow-lg transition-transform">
              <div className="flex justify-between items-start mb-4">
                <div className="p-3 rounded bg-slate-900/50 border border-slate-700 text-cyan-400 group-hover:text-cyan-300 group-hover:border-cyan-500/50 transition-colors">
                  <exp.icon size={24} />
                </div>
                <span className="text-[10px] font-mono text-slate-600 font-bold border border-slate-800 px-1.5 py-0.5 rounded group-hover:text-cyan-500 group-hover:border-cyan-900">
                  {exp.code}
                </span>
              </div>

              <h3 className="text-lg font-bold text-slate-200 mb-2 group-hover:text-cyan-400">{exp.title}</h3>
              <p className="text-sm text-slate-500 leading-relaxed mb-6 flex-1">{exp.desc}</p>

              <button
                onClick={() => onSelectExperiment && onSelectExperiment(exp.id)}
                className="w-full py-2 rounded border border-slate-700 text-xs font-bold uppercase tracking-widest text-slate-400 hover:bg-cyan-500 hover:text-white hover:border-cyan-500 transition-all flex items-center justify-center gap-2 group-hover:bg-slate-800"
              >
                Truy cập <ArrowUpRight size={14} />
              </button>
            </HoloCard>
          ))}
        </div>
      </div>

      {/* Custom Modules Section */}

    </div>
  )
}